    //   const eventDelta = e.wheelDelta || -e.deltaY * 40
    //   const $scrollWrapper = this.scrollWrapper
    //   $scrollWrapper.scrollLeft = $scrollWrapper.scrollLeft + eventDelta / 4
    // },
    // emitScroll() {
    //   this.$emit('scroll')
    // },

    /**
     * @name: moveToTarget
     * @msg: 标签移动核心代码,其他可以不用
     *  api定义:
     * scrollLeft:属性设置或返回元素内容向左滚动(水平方向)的像素数。当前可视窗口距离距离左侧某元素的距离
     * offsetWidth:获取元素的宽度，包含内边距（padding）和边框（border）。
     * scrollWidth：获取元素的宽度，包含内边距（padding）
     * @param {*}
     * @return {*}
     */
    moveToTarget: function moveToTarget(currentTag, parentTagList) {
      var $container = this.$refs.scrollContainer.$el; // 标签容器

      var $containerWidth = $container.offsetWidth; // tag元素当前可视的宽度

      var $scrollWrapper = this.scrollWrapper;
      var tagList = parentTagList; // 所有tag标签实例

      var firstTag = null; // 当前tagList的第一条

      var lastTag = null; // 当前tagList的最后一条
      // 长度大于0时，获取数组的第一个标签跟最后一个标签

      if (tagList.length > 0) {
        firstTag = tagList[0];
        lastTag = tagList[tagList.length - 1];
      } // 当第一条数据跟当前选中的一样时，元素距离左侧滚动的距离为0


      if (firstTag.to.path === currentTag.to.path) {
        $scrollWrapper.scrollLeft = 0;
      } else if (lastTag.to.path === currentTag.to.path) {
        // 点击左侧菜单可以让标签移动
        $scrollWrapper.scrollLeft = $scrollWrapper.scrollWidth - $containerWidth;
      } else {
        // find preTag and nextTag
        var currentIndex = tagList.findIndex(function (item) {
          return item.to.path === currentTag.to.path;
        });
        var prevTag = tagList[currentIndex - 1]; // 当前选中的下一个兄弟标签

        var nextTag = tagList[currentIndex + 1]; // 当前选中的上一个兄弟标签

        var afterNextTagOffsetLeft = nextTag.$el.offsetLeft + nextTag.$el.offsetWidth + tagAndTagSpacing;
        var beforePrevTagOffsetLeft = prevTag.$el.offsetLeft - tagAndTagSpacing;

        if (afterNextTagOffsetLeft > $scrollWrapper.scrollLeft + $containerWidth) {
          $scrollWrapper.scrollLeft = afterNextTagOffsetLeft - $containerWidth;
        } else if (beforePrevTagOffsetLeft < $scrollWrapper.scrollLeft) {
          $scrollWrapper.scrollLeft = beforePrevTagOffsetLeft;
        }
      }
    }
  }
};